/// <reference path="../../tsd.d.ts" />

module cx.todos {

  export interface Tag {
    text: string;
  }

  export interface BasicTodoItem {
    id?: string;
    title?: string;
  }

  export interface RestTodoItem extends BasicTodoItem {
    createdDate?: number;
    dueDate?: number;
    tags?: string[];
  }

  export interface TodoItem extends BasicTodoItem {
    createdDate?: Date;
    dueDate?: Date;
    tags?: Tag[];
  }

  export interface TodoFilter {
    id?: string;
    title?: string;
    tags?: string[];
  }

  export interface PagedTodosResponse {
    content: TodoItem[];
    size: number;
    totalElements: number;
  }

  export class TodosService {

    /** @ngInject */
    constructor(private CxRestangular:restangular.IService) {}

    private buildSortString(sorting) {
      var result = "";
      for(var key in sorting) {
        result += key + "," + sorting[key];
        break;
      }
      return result;
    }

    private buildQuery(params) {
      console.log(params);
      console.log(params.filter());
      console.log(params.sorting());

      var filter = params.filter();

      console.log(this.buildSortString(params.sorting()));

      return {
        title: filter.title ? filter.title : "",
        tags: filter.tags ? filter.tags : [],
        page: params.page() - 1,
        size: params.count(),
        sort: this.buildSortString(params.sorting())
      }
    }

    convertToTodoItem(item: RestTodoItem) {
      var res: TodoItem = {
        id: item.id,
        title: item.title,
        tags: item.tags.map(tag => {return {text: tag}}),
        createdDate: item.createdDate ? new Date(item.createdDate) : null,
        dueDate: item.dueDate ? new Date(item.dueDate) : null
      };
      return res;
    }

    convertToRestTodoItem(item: TodoItem) {
      console.log(item);
      var res: RestTodoItem = {
        id: item.id,
        title: item.title,
        tags: item.tags.map(tag => tag.text),
        createdDate: item.createdDate ? item.createdDate.valueOf() : null,
        dueDate: item.dueDate ? item.dueDate.valueOf() : null,
      };
      console.log(res);
      return res;
    }

    convertToTodoItems(items: RestTodoItem[]): TodoItem[] {
      return items.map((item) => this.convertToTodoItem(item));
    }

    convertToRestTodoItems(items: TodoItem[]): RestTodoItem[] {
      return items.map((item) => this.convertToRestTodoItem(item));
    }

    getAllTodos(params:any): ng.IPromise<PagedTodosResponse> {
      var query = this.buildQuery(params);
      return this.CxRestangular.all("todos").customGET("", query).then(
        (pagedTodosResponse) => {
          pagedTodosResponse.content = this.convertToTodoItems(pagedTodosResponse.content);
          return pagedTodosResponse;
      });
    }

    getTodo(id: string): ng.IPromise<TodoItem> {
      return this.CxRestangular.one("todos", id).get().then((restItem) => this.convertToTodoItem(restItem));
    }

    createTodo(item: TodoItem): ng.IPromise<TodoItem> {
      var restItem = this.convertToRestTodoItem(item);
      return this.CxRestangular.all("todos").customPOST(restItem);
    }

    updateTodo(item: TodoItem): ng.IPromise<TodoItem> {
      var restItem = this.convertToRestTodoItem(item);
      return this.CxRestangular.one("todos", restItem.id).customPUT(restItem);
    }

    deleteTodo(itemId: string) {
      return this.CxRestangular.one("todos", itemId).remove();
    }
  }

  export class TodosCtrl {

    constructor(private TodosService:TodosService, private ngTableParams:any, private $state: ng.ui.IStateService) {
    }

    formatTags = function(tags: Tag[]): string {
      return tags.map((tag) => tag.text).join(', ');
    }

    loadItems($defer:ng.IDeferred<TodoItem[]>, params:any): void {
      this.TodosService.getAllTodos(params).then(function(pagedTodoResponse) {
        params.total(pagedTodoResponse.totalElements);
        $defer.resolve(pagedTodoResponse.content);
      });
    }

    tableParams = new this.ngTableParams({
      page: 1,
      count: 10,
      sorting: {
        id: 'asc'
      }
    }, {
      counts: [],
      getData: this.loadItems.bind(this)
    });

    gotoDetails(item: TodoItem) {
      this.$state.go('todos.detail', {todoId: item.id})
    }

  }

  export class TodoDetailsCtrl {

    selectedItem: TodoItem;
    success: string;
    err: string;

    dueDatePickerOpened = false;
    dateOptions = {
      formatYear: 'yyyy',
      startingDay: 1
    };

    constructor(private TodosService:TodosService, private $stateParams: ng.ui.IStateParams, private $state: ng.ui.IStateService) {
      var id = $stateParams['todoId'];
      console.log("fetching item: ", id);
      this.getDetails(id);
    }

    openDueDatePicker($event) {
      $event.preventDefault();
      $event.stopPropagation();
      this.dueDatePickerOpened = true;
    }

    getDetails(id: string) {
      this.TodosService.getTodo(id).then((item) => this.selectedItem = item);
    }

    updateTodo(item: TodoItem) {
      console.log("updating item: ", item);
      this.TodosService.updateTodo(item).then((resp)=> this.setSuccess(), (resp) => this.setFailure(resp));
    }

    deleteTodo(item:TodoItem) {
      this.TodosService.deleteTodo(item.id).then(
        () => this.$state.go("todos.list"),
        (resp) => this.setFailure(resp));
    }

    private setSuccess() {
      this.success= "successfully updated.";
      this.err = null;
    }

    private setFailure(resp) {
      this.err = "error updating: " + resp.status;
      this.success = null;
    }
  }

  export class TodoCreationCtrl {
    item: TodoItem = {};
    success = null;
    failure = null;

    dueDatePickerOpened = false;
    dateOptions = {
      formatYear: 'yyyy',
      startingDay: 1
    };

    constructor(private TodosService: TodosService, private TagsService: TagsService, private $state: ng.ui.IStateService) {}

    openDueDatePicker($event) {
      $event.preventDefault();
      $event.stopPropagation();
      this.dueDatePickerOpened = true;
    }

    loadAutoCompleteTags($query:string): ng.IPromise<string[]> {
      console.log($query);
      //return this.TagsService.getAllTags($query).then((suggestions) => {console.log(suggestions); return suggestions});
      return this.TagsService.getAllTags($query);
    }

    createTodo(item: TodoItem) {
      console.log(item);
      this.TodosService.createTodo(item).then(
        (respitem) => {this.failure = null; this.success = "successfully created item"; this.$state.go("todos.detail", {todoId: respitem.id})},
        (err) => {this.success = null; this.failure = err.status}
      );
    }
  }

  export class TagsService {
    constructor(private $timeout, private $q: ng.IQService, private CxRestangular: restangular.IService) {}

    getAllTags(query: string): ng.IPromise<string[]> {
      console.log(query);

      var deferred = this.$q.defer();
      var res = ['freizeit', 'hochschule', 'arbeit', 'fun', 'einkaufen', 'links', 'thesis', 'doku', 'geheim', 'vortrag',
        'kochen', 'backen', 'rezept', 'garten', 'tutorial'];

      var matchesCurrentQuery = (tag:string) => (tag.lastIndexOf(query,0) == 0);
      this.$timeout(() => {
          console.log(res);
          deferred.resolve(res.filter((tag) => matchesCurrentQuery(tag) ));
        },
        0);

      console.log(deferred.promise);
      return deferred.promise;
    }
  }
}
