/// <reference path="../../tsd.d.ts" />

module cx.translation {
  'use strict';

  /*
   * Configuration for internationalization. Tells the translateProvider where to search for the translations and to
   * determine the preferred language of the client.
   *
   * @ngInject
   */
  export function configureTranslateProvider ($translateProvider: ng.translate.ITranslateProvider) {
     $translateProvider
      .useStaticFilesLoader({
        prefix: 'i18n/',
        suffix: '.json'
      })

      .fallbackLanguage('en')
      .registerAvailableLanguageKeys(['en', 'de'], {
       'en*': 'en',
       'de*': 'de'
       })
      .determinePreferredLanguage()
      .useLocalStorage();
  }
}
